// Angular
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
// Lodash
import { shuffle } from 'lodash';
import { Subscription } from 'rxjs';
// Services
// Widgets model
import { LayoutConfigService, SparklineChartOptions } from '../../../core/_base/layout';
import { Widget4Data } from '../../partials/content/widgets/widget4/widget4.component';
import globalVariables from '../globalVariables';
// NGRX
import { Store, ActionsSubject } from '@ngrx/store';
import { AppState } from '../../../core/reducers';

@Component({
	selector: 'kt-dashboard',
	templateUrl: './dashboard.component.html',
	styleUrls: ['dashboard.component.scss'],
})
export class DashboardComponent implements OnInit, OnDestroy {
	closeResult: string;
	boxData = {
		ilsCash: { text: 'كاش', value: '' },
		ilsCheck: { text: 'شيكات', value: '' },
	}

	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};

	constructor(private layoutConfigService: LayoutConfigService,
		private http: HttpClient,
		private store: Store<AppState>,
	) {
	}

	ngOnInit(): void {
		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getSafeInfo();
	}

	/**
 * On Destroy
 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}


	getSafeInfo() {
		this.http.get(
			globalVariables.baseUrl + 'api/safe/PaymentMethods',
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.boxData.ilsCash.value = res.data.cash;
				this.boxData.ilsCheck.value = res.data.check;
			}, (err) => {
			});
	}
}
