// Angular
import { Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, Output, EventEmitter } from '@angular/core';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatSnackBar, MatDialog, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, delay, take } from 'rxjs/operators';
import { fromEvent, merge, Subscription, of } from 'rxjs';
// Translate Module
import { TranslateService } from '@ngx-translate/core';
// NGRX
import { Store, ActionsSubject } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';
// Services and Models
import { BoxDetailsModel } from './box-details.model';
// Components
// Http
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../globalVariables';
import { LayoutUtilsService, MessageType } from '../../../../core/_base/crud';
import { BoxDetailsEditDialogComponent } from './box-details-edit/box-details-edit.dialog.component';

// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/compgetItemCssClassByStatusonents/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	// tslint:disable-next-line:component-selector
	selector: 'box-details',
	templateUrl: 'box-details.component.html',
	styleUrls: ['box-details.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,

})
export class BoxDetailsComponent implements OnInit, OnDestroy {
	@Output() refreshSafeInfo = new EventEmitter<any>();
	// Table fields
	dataSource: MatTableDataSource<BoxDetailsModel>;
	displayedColumns = ['safeTransactionDate', 'inOutType', 'transactionType', 'note', 'value', 'balance', 'actions'];
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	@ViewChild('sort1', { static: true }) sort: MatSort;
	isLoadingResults = false;
	// Filter fields
	@ViewChild('searchInput', { static: true }) searchInput: ElementRef;
	filterStatus = '';
	selectedEmployee = '';
	selectedFuelType = '';
	selectedGasPump = '';
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	dateFrom = new Date(+new Date(this.date.getFullYear(), this.date.getMonth(), 1) - this.tzoffset).toISOString().slice(0, 16);
	dateTo = new Date(+new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0) - this.tzoffset).toISOString().slice(0, 16);
	// Filter data
	// TODO: Get using APIs
	employees = [];
	fuelTypes = [];
	gasPumps = [];
	// Selection
	selection = new SelectionModel<BoxDetailsModel>(true, []);
	customersResult: BoxDetailsModel[] = [];
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	// Language
	languageText = {
		empty: 'لا يوجد بيانات لعرضها',
		searching: 'جار البحث ...',
		search: 'بحث',
		filterEmptyMessage: 'لا يوجد بيانات مطابقة ل ',
		confirm: 'تأكيد',
		close: 'الغاء',
		add: 'اضافة',
		itemsPerPage: 'عدد الصفوف بالصفحة:',
		firstPage: 'الصفحة الأولى',
		lastpage: 'الصفحة الأخيرة',
		previousPage: 'الصفحة السابقة',
		nextPage: 'الصفحة التالية'
	};

	/**
	 * Component constructor
	 *
	 * @param layoutUtilsService
	 * @param dialog
	 * @param snackBar: MatSnackBar
	 * @param store: Store<AppState>
	 * @param http
	 */
	constructor(
		private layoutUtilsService: LayoutUtilsService,
		public dialog: MatDialog,
		public snackBar: MatSnackBar,
		private store: Store<AppState>,
		private http: HttpClient
	) { }

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		// Init DataSource
		this.dataSource = new MatTableDataSource();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.loadBoxDetailsList();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	arabicRangeLabel = (page: number, pageSize: number, length: number) => {
		if (length === 0 || pageSize === 0) {
			return `0 من ${length}`;
		}

		length = Math.max(length, 0);

		const startIndex = page * pageSize;

		// If the start index exceeds the list length, do not try and fix the end index to the end.
		const endIndex = startIndex < length ?
			Math.min(startIndex + pageSize, length) :
			startIndex + pageSize;

		return `${startIndex + 1} - ${endIndex} من ${length}`;
	};

	/**
	 * Load BoxDetails List from service through data-source
	 */
	loadBoxDetailsList(query = '') {
		this.isLoadingResults = true;
		let dateFrom = new Date(+new Date(this.dateFrom) - this.tzoffset).toISOString().slice(0, 16);
		let dateTo = new Date(+new Date(this.dateTo) - this.tzoffset).toISOString().slice(0, 16);
		let url = '?FromDate=' + dateFrom + '&ToDate=' + dateTo + '&SerachParam=' + query;

		this.http.get(
			globalVariables.baseUrl + 'api/safe' + url,
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				if (res.success) {
					this.isLoadingResults = false;
					let data = [];
					let balance = 0;
					for (let index = res.data.length - 1; index > 0; index--) {
						const item = res.data[index];

						if (item.inOutType === "out")
							balance -= item.value;
						else
							balance += item.value;

						data.push({
							...item,
							balance,
							safeTransactionDate: item.createdAt
						});
					}

					this.dataSource = new MatTableDataSource(data.reverse());

					if (this.paginator) {
						this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
						this.paginator._intl.firstPageLabel = this.languageText.firstPage;
						this.paginator._intl.previousPageLabel = this.languageText.previousPage;
						this.paginator._intl.nextPageLabel = this.languageText.nextPage;
						this.paginator._intl.lastPageLabel = this.languageText.lastpage;

						this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
					}

					this.dataSource.paginator = this.paginator;
					this.dataSource.sort = this.sort;
				}
			}, (err) => {
				this.isLoadingResults = false;

				this.dataSource.data = [];

				if (this.paginator) {
					this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
					this.paginator._intl.firstPageLabel = this.languageText.firstPage;
					this.paginator._intl.previousPageLabel = this.languageText.previousPage;
					this.paginator._intl.nextPageLabel = this.languageText.nextPage;
					this.paginator._intl.lastPageLabel = this.languageText.lastpage;

					this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
				}

				this.dataSource.paginator = this.paginator;
				this.dataSource.sort = this.sort;
			});
	}

	/**
	 * Show add Move dialog
	 */
	addMove() {
		const newReading = new BoxDetailsModel();
		newReading.clear(); // Set all defaults fields
		this.editExpense(newReading);
	}

	/**
	 * Show Edit boxDetails dialog and save after success close result
	 * @param boxDetails: BoxDetailsModel
	 */
	editExpense(boxDetails: BoxDetailsModel) {
		let saveMessageTranslateParam = boxDetails.id <= 0 ? 'تم الاضافة بنجاح' : 'تمت التعديل بنجاح';
		const _saveMessage = saveMessageTranslateParam;
		const _messageType = boxDetails.id > 0 ? MessageType.Update : MessageType.Create;
		const dialogRef = this.dialog.open(BoxDetailsEditDialogComponent, {
			data: {
				boxDetails
			}
		});
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType);
			this.loadBoxDetailsList();
			if (boxDetails.id <= 0)
				this.refreshSafeInfo.emit();
		});
	}

	/**
	 * Get file name of the exported excel file
	 */
	getExcelFileName() {
		return 'تفاصيل الصندوق' + '-' + new Date().toISOString().slice(0, 10);
	}
}
