// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of, Observable } from 'rxjs';
import { delay, map, startWith } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';
import { BoxDetailsModel } from '../box-details.model';
import moment from "moment";

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'gas-box-details-edit-dialog',
	templateUrl: 'box-details-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class BoxDetailsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	boxDetails: BoxDetailsModel;
	expenseForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	// TODO: Get from Api
	paymentTypes = [];
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	myControl = new FormControl();
	filteredOptions: Observable<any[]>;
	selectedPumpId = '';
	isFinalOperatingExpense = false;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};
	role = '';

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<BoxDetailsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<BoxDetailsEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.boxDetails = this.data.boxDetails;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.role = localStorage.getItem('role');
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.getPaymentTypes();
		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	createForm() {
		let date = this.boxDetails.safeTransactionDate;
		// if (this.boxDetails.id <= 0)
		// 	date = new Date(+new Date(this.boxDetails.safeTransactionDate) - this.tzoffset).toISOString().substr(0, 16);

		this.expenseForm = this.fb.group({
			id: [this.boxDetails.id, Validators.required],
			value: [this.boxDetails.value, this.boxDetails.id > 0 ? '' : Validators.required],
			date: [date, this.boxDetails.id > 0 ? '' : Validators.required],
			paymentType: [this.boxDetails.paymentMethodsId, this.boxDetails.id > 0 ? '' : Validators.required],
			note: [this.boxDetails.note, this.boxDetails.id > 0 ? Validators.required : '']
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.boxDetails.id > 0) {
			return `تعديل الحركة`;
		}

		return 'ايداع في البنك';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.expenseForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getPaymentTypes() {
		this.http.get(
			globalVariables.baseUrl + 'api/PaymentsMethods',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.paymentTypes = res.data.map(item => {
				return {
					name: item.paymentType,
					id: item.id
				};
			});
		});
	}

	/**
	 * Returns prepared customer
	 */
	prepareOperatingExpense(): BoxDetailsModel {
		const controls = this.expenseForm.controls;
		const _safe = new BoxDetailsModel();
		_safe.id = this.boxDetails.id;
		_safe.value = controls.value.value;
		_safe.safeTransactionDate = controls.date.value;
		_safe.paymentMethodsId = controls.paymentType.value;
		_safe.note = controls.note.value;

		return _safe;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.expenseForm.controls;

		/** check form */
		if (this.expenseForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedOperatingExpense = this.prepareOperatingExpense();
		if (editedOperatingExpense.id > 0) {
			this.updateOperatingExpense(editedOperatingExpense);
		} else {
			this.createOperatingExpense(editedOperatingExpense);
		}
	}

	/**
	 * Update customer
	 *
	 * @param _safe: BoxDetailsModel
	 */
	updateOperatingExpense(_safe: BoxDetailsModel) {

		console.log(_safe.safeTransactionDate);
		var TransDate = moment(_safe.safeTransactionDate).format("YYYY-MM-DDTHH:mm:00");

		console.log(TransDate)

		this.http.put(
			globalVariables.baseUrl + 'api/safe',
			{
				..._safe,
				value: +_safe.value,
				createdAt:  TransDate
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _safe, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل الحركة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create customer
	 *
	 * @param _safe: BoxDetailsModel
	 */
	createOperatingExpense(_safe: BoxDetailsModel) {
		let date = new Date(+new Date(this.boxDetails.safeTransactionDate) - this.tzoffset).toISOString().substr(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/Safe/Bank/Transactions',
			{
				..._safe,
				value: +_safe.value,
				createdAt: date
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _safe, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة الحركة';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
