export class BoxDetailsModel {
	id: number;
	inOutType: string;
	transactionType: string;
	note: string;
	value: string;
	balance: string;
	safeTransactionDate: string;
	paymentMethodsId: number;
	isEditable: boolean;

	clear() {
		this.id = 0;
		this.inOutType = '';
		this.transactionType = '';
		this.note = '';
		this.value = '';
		this.paymentMethodsId = 0;
		this.safeTransactionDate = new Date().toISOString().substr(0, 16);
	}
}
